import React from 'react';

class Tile extends React.Component {
  render() {
    const link = this.props.link;
    const label = this.props.label;
    const heading = this.props.heading;
    const desc = this.props.desc;
    const imgUrl = this.props.imgUrl;

    return (
      <a className="tile" ref="link" href={link} target="_blank" onFocus={this.addHoverState} onBlur={this.removeHoverState}>
        <div className="tile__info">
          <h1 className="tile__heading">{heading}</h1>
          <p className="tile__desc">{desc}</p>
        </div>
        <img className="tile__img" alt="project image" src={imgUrl} />
      </a>
    )
  }
};

export default Tile;



