const React = require('react');
import SocialMediaBtn from './SocialMediaBtn';
import SocialMedia from './SocialMedia';
import { Link } from 'react-router';

export default class SideHeader extends React.Component {
  constructor() {
    super();
  }

  scrollToSection = (evt) => {
    const links = [... document.querySelectorAll('.page-link')];
    console.log(links);
    links.forEach(link => {
      if (evt.currentTarget === link) {
        link.parentElement.classList.add('active');
      } else {
        link.parentElement.classList.remove('active');
      }
    })
    const id = evt.currentTarget.hash.substring(1);
    const section = document.getElementById(id);
    if (section) {
      let element = section.getBoundingClientRect().top;
      if (id === 'projects') {
        element = 856;
      } else if (id === 'about') {
        element = 1597;
      }
      if (id === 'projects' || id === 'about') {
        const body = document.body.getBoundingClientRect().top;
        const offset = element - 0;
        if (document.getElementById(id)) {
          window.scrollTo(0, (offset - 100));
        }
      }
    }
  }

  render() {
    return (
      <nav className="sidenav">
        <ul className="menu">
          <li><Link onClick={this.scrollToSection} className="page-link" to="/">Home</Link></li>
          <li><a onClick={this.scrollToSection} className="page-link" href="./#projects">Projects</a></li>
          <li><Link onClick={this.scrollToSection} className="page-link" to="/blog">Blog</Link></li>
          <li><Link onClick={this.scrollToSection} className="page-link" to="/contact">Get in touch</Link></li>
          <li><SocialMedia /></li>
        </ul>
      </nav>
    )
  }
};



