import React from 'react';
import { Link } from 'react-router';

class RecentPost extends React.Component {

  render() {
    const fixed = this.props.title.toLowerCase().replace(/\s/g, '-').replace(',', '');
    const newTitle = `/blog/${fixed}`;

    return (
      <li className="recent-posts__post">
        <Link to={newTitle}>
          <p className="tag" data-tag={this.props.tag}>{this.props.tag}</p>
          <h1>{this.props.title}</h1>
        </Link>
        <p className="date">{this.props.date}</p>
      </li>
    );
  }
};

export default RecentPost;



