'use strict';

import React from 'react';
import ReactDOM from 'react-dom';
import classnames from 'classnames';
import 'babel-polyfill';

import SideNav from './home/SideNav';
import Footer from './home/Footer';

// <App />
export default class App extends React.Component {

  constructor(props) {
    super(props);

    setTimeout(() => {
      [... document.querySelectorAll('.social-media__btn')].forEach(btn => {
        btn.style.opacity = '1';
      });
    }, 1000);
  }

  componentDidMount() {
    window.addEventListener('scroll', (evt) => {
      this.checkScroll();
    });
  }

  checkScroll() {
    let topPos = window.scrollY;
    let height = 152;
    if (topPos > height) {
      document.querySelector('.sidenav').classList.add('top');
    } else if (topPos < height){
      document.querySelector('.sidenav').classList.remove('top');
    } else if (topPos === 0) {
      document.querySelector('.sidenav').classList.remove('top');
    }
    let projects;
    if (document.querySelector('#projects')) {
      projects = document.querySelector('#projects').getBoundingClientRect().top;
    }
    if (projects) {
      const body = document.body.getBoundingClientRect().top;
      const offset = projects - body;
      const links = document.querySelectorAll('.page-link');
      if (topPos > 180) {
        setTimeout(() => {
          document.querySelector('.projects').classList.add('expand');
        }, 300);
        links.forEach(link => {
          if (topPos < 1100) {
            if (link.hash === '#projects') {
              link.parentElement.classList.add('active');
            }
          } else {
            link.parentElement.classList.remove('active');
          }
        });
      } else {
        links.forEach(link => {
          if (link.hash === '#projects') {
            link.parentElement.classList.remove('active');
          }
        });
      }
    }
  }

  render() {
    const path = window.location.pathname.split('/');
    const isBlog = path[1] == 'blog';
    const isExpanded = path[2] == undefined;
    const classNames = classnames({
      'container': !isBlog,
      'blog-container': isBlog,
      'blog-container--expanded': !isExpanded
    });

    return (
    <div>
      <div className={classNames}>
        <SideNav />
          {this.props.children}
        <Footer />
      </div>
    </div>
    )
  }
};



