import React from 'react';
import { Link } from 'react-router';
import articles from '../../data/articles';
import Markdown from 'react-remarkable';

import Categories from './Categories';
import Category from './Category';
import RecentPosts from './RecentPosts';
import RecentPost from './RecentPost';
import BlogFooter from './BlogFooter';
import BlogPosts from './BlogPosts';
import BlogPost from './BlogPost';

class Blog extends React.Component {
  state = {
    currentTag: 'ALL'
  }

  switchCategory = (category) => {
    this.blogLeft.parentElement.parentElement.classList.remove('blog-container--expanded');
    this.blogLeft.parentElement.parentElement.classList.add('blog-container--mid-expanded');
    const tag = category;
    this.setState({
      currentTag: tag
    });
  }

  backToHome = () => {
    window.scrollTo(0, 0);
    const activeItem = document.querySelector('.sidenav .menu .active');
    activeItem.classList.remove('active');
  }

  render() {
    let recentPostsHeading = this.state.currentTag == 'ALL' ? ( 'All recent posts' ) : ( `Recent ${this.state.currentTag.toLowerCase()} posts` );
    let recentPosts = [];
    Object.keys(articles).map(article => {
      const newTag = articles[article].tag.toUpperCase();
      if ((newTag === this.state.currentTag) || (this.state.currentTag === 'ALL')) {
        recentPosts.push(<RecentPost key={article} tag={articles[article].tag} title={articles[article].title} date={articles[article].date} />);
      }
    });

    return (
      <div className="blog">
        <div className="blog__left"  ref={(ref) => { this.blogLeft = ref; }}>
          <h1 className="blog__logo"><Link onClick={this.backToHome} to="/" >marijohannessen.com</Link><span> / <Link to="/blog">blog</Link></span></h1>
          <Categories>
            <Category onClick={this.switchCategory} title="ALL" selected={"ALL" === this.state.currentTag} />
            <Category onClick={this.switchCategory} title="CODE" selected={"CODE" === this.state.currentTag} />
            <Category onClick={this.switchCategory} title="CREATIVE" selected={"CREATIVE" === this.state.currentTag} />
            <Category onClick={this.switchCategory} title="LIFE" selected={"LIFE" === this.state.currentTag} />
          </Categories>
          <RecentPosts heading="Recent Posts">
            {recentPosts}
          </RecentPosts>
          <BlogFooter />
        </div>
        <div className="blog__right">
          <BlogPosts>
            {Object.keys(articles).map(article => {
              return <BlogPost key={article} tag={articles[article].tag} title={articles[article].title} date={articles[article].date} content={articles[article].__content} />
            })}
          </BlogPosts>
          {this.props.children}
        </div>
      </div>
    );
  }
};

export default Blog;



