import React from 'react';
import classnames from 'classnames';
import { Link } from 'react-router';

class BlogFooter extends React.Component {
  state = {
    showAbout: false
  }

  onClick = () => {
    this.setState({
      showAbout: !this.state.showAbout
    })
  }

  onMouseLeave = () => {
    this.setState({
      showAbout: false
    })
  }

  render() {
    return (
      <div className="blog-footer" onMouseLeave={this.onMouseLeave}>
        <ul className="blog-footer-links">
          <li>
            <p className="blog-about">
              This blog is created by me, <Link to="/">Mari Johannessen</Link>. I am a Front-End Developer based in Austin, TX. I blog to learn, grow, inspire and remember.
            </p>
          </li>
        </ul>
      </div>
    );
  }
};

export default BlogFooter;



